.tagline{
    margin-top: auto;
    /* float:right; */
    margin-bottom:auto;
    text-align: right;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    
}


 .jumbotron{
    margin: auto;
    height:auto;
    background-color:#f3f5f6 !important;
} 

.btn-primary{
    width:120px;
    float:right; 
    margin-right: 100px;
}

.lead{margin-right: 100px !important;
    text-align:right;
    font-size: 15px;
    
}
.tagline{
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-name: bounceIn;
            animation-name: bounceIn;
  }
  
  @-webkit-keyframes bounceIn {
    0% {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        opacity: 0;
      }
      60% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 1;
      }
      100% {
        -webkit-transform: scale(1);
                transform: scale(1);
      }
    }
  
  @keyframes bounceIn {
    0% {
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        opacity: 0;
      }
      60% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        opacity: 1;
      }
      100% {
        -webkit-transform: scale(1);
                transform: scale(1);
      }
    }

.container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-direction: column;
        flex-direction: column;
}





